class LemonText {
    value;
    font;
    fontStyle;
    fontWeight;
    color;
    posX;
    posY;
    zIndex;
    align;
    width;
    spacing;
    constructor() {}
}
class LemonImage {
    frame;
    frameColor;
    width;
    height;
    mask;
    zIndex;
    transform;
    posX;
    posY;
    align;
    constructor() {}
}
class LemonMeta {
    video = {
        fill: '',
        width: '',
        height: '',
        waterMark: '',
        waterMarkType: '',
    };
    waveform = {
        type: '',
        fill: '',
        stroke: '',
        design: '',
        width: '',
        height: '',
        posX: '',
        posY: '',
    };
    tracker = {
        display: '',
        type: '',
        fill: '',
    };
    constructor() {}
}

class Template_01_Header extends LemonText {
    constructor(value) {
        super()
        this.value = value
        this.font = "Montserrat Bold";
        this.fontSize = "60";
        this.fontStyle = "NORMAL";
        this.fontWeight = "BOLD";
        this.color = "#005d93";
        this.posX = "61";
        this.posY = "1026";
        this.zIndex = "0";
        this.align = "MIDDLE";
        this.width = "750";
        this.spacing = "NORMAL";
    }
}

class Template_01_SubHeader extends LemonText {
    constructor(value) {
        super()
        this.value = value
        this.font = "Montserrat Bold";
        this.fontSize = "23";
        this.fontStyle = "NORMAL";
        this.fontWeight = "BOLD";
        this.color = "#ffffff";
        this.posX = "61";
        this.posY = "1090";
        this.zIndex = "1";
        this.align = "MIDDLE";
        this.width = "750";
        this.spacing = "NORMAL";
    }
}
class Template_01_Description extends LemonText {
    constructor(value) {
        super()
        this.value = value
        this.font = "Montserrat Bold";
        this.fontSize = "19";
        this.fontStyle = "NORMAL";
        this.fontWeight = "THIN";
        this.color = "#005d93";
        this.posX = "82";
        this.posY = "1156";
        this.zIndex = "2";
        this.align = "MIDDLE";
        this.width = "709";
        this.spacing = "NORMAL";
    }
}
class Template_01_Foreground extends LemonImage {
    constructor() {
        super()
        this.frame = "NONE";
        this.frameColor = "#1F657E";
        this.width = "468";
        this.height = "600";
        this.mask = "NONE";
        this.zIndex = "2";
        this.transform = "none";
        this.posX = "200";
        this.posY = "330";
        this.align = "CENTER";
    }
}
class Template_01_Logo extends LemonImage {
    constructor() {
        super()
        this.frame = "NONE";
        this.frameColor = "#1F657E";
        this.width = "864";
        this.height = "1536";
        this.mask = "NONE";
        this.zIndex = "0";
        this.transform = "none";
        this.posX = "0";
        this.posY = "0";
        this.align = "CENTER";
    }
}
class Template_01_Background extends LemonImage {
    constructor() {
        super()
        this.frame = "NONE";
        this.frameColor = "#1F657E";
        this.width = "864";
        this.height = "1536";
        this.mask = "NONE";
        this.zIndex = "0";
        this.transform = "none";
        this.posX = "0";
        this.posY = "0";
        this.align = "CENTER";
    }
}

class Template_01_Meta extends LemonMeta {
    constructor(value) {
        super()
        this.video.value = value
        this.video.fill = "#1f1f1f";
        this.video.width = "864";
        this.video.height = "1536";
        this.video.waterMark = "true";
        this.video.waterMarkType = "light";

        this.waveform.type = "SAD";
        this.waveform.fill = "#005d93";
        this.waveform.stroke = "none";
        this.waveform.design = "DEFAULT";
        this.waveform.width = "864";
        this.waveform.height = "100";
        this.waveform.posX = "0";
        this.waveform.posY = "1430";

        this.tracker.display = "false";
        this.tracker.type = "BOX_BORDER";
        this.tracker.fill = "#71CCD6";
    }
}

export function template_01(header, subHeader, description) {
    return {
        header: new Template_01_Header(header),
        subHeader: new Template_01_SubHeader(subHeader),
        description: new Template_01_Description(description),
        meta: new Template_01_Meta(),
        foreground: new Template_01_Foreground(),
        background: new Template_01_Background(),
        logo: new Template_01_Logo()
    };
}