<template>
  <div class="create-wrapper">
    <LemonNav is-light />
    <div class="main-content">
      <div class="carousel">
        <v-carousel hide-delimiters :show-arrows="false" v-model="model">
          <v-carousel-item class="template-wrap">
            <img src="../assets/template_01.png" class="template" alt />
          </v-carousel-item>
          <v-carousel-item class="template-wrap">
            <img src="../assets/template_01.png" class="template" alt />
          </v-carousel-item>
        </v-carousel>
        <div class="control-btns">
          <v-btn class="ma-2" depressed x-large color="transparent" dark @click="slideBackward">
            <v-icon dark large>mdi-arrow-left-circle</v-icon>
          </v-btn>
          <v-btn class="ma-2" depressed x-large color="transparent" dark @click="slideFoward">
            <v-icon dark large>mdi-arrow-right-circle</v-icon>
          </v-btn>
        </div>
      </div>
      <div class="form-section">
        <v-text-field dark label="Header" clearable v-model="header"></v-text-field>
        <v-text-field dark label="SubHeader" clearable v-model="subHeader"></v-text-field>
        <v-text-field dark label="Description" clearable v-model="description"></v-text-field>
        <v-file-input dark accept="image/jpeg, image/png*" label="Select an Image" v-model="image"></v-file-input>
        <v-file-input
          accept="audio/wav, audio/mp3, audio/ogg*"
          label="Select an Audio File"
          v-model="audio"
          dark
        ></v-file-input>

        <div class="dl-btns">
          <LemonButton @lemonBtnClick="click" class="btn-generate">Generate your video</LemonButton>
          <LemonButton @lemonBtnClick="downloadVideo" v-if="download" class="btn-generate">Download</LemonButton>
        </div>
        <v-progress-linear
          :indeterminate="progressive"
          color="white"
          height="5"
          :value="progress"
          v-if="showProgress"
        ></v-progress-linear>
        <p class="task-label">{{status}}</p>
      </div>
    </div>
    <LemonFooter is-light></LemonFooter>
  </div>
</template>

<script>
import LemonNav from "@/components/LemonNav.vue";
import LemonButton from "@/components/LemonButton.vue";
import LemonFooter from "@/components/LemonFooter.vue";
import * as script from "../assets/js/index";
import $ from "jquery";

export default {
  components: {
    LemonNav,
    LemonButton,
    LemonFooter
  },
  data() {
    return {
      colors: ["primary", "secondary", "yellow darken-2", "red", "orange"],
      model: 0,
      header: "",
      subHeader: "",
      description: "",
      audio: null,
      image: null,
      power: 78,
      knowledge: 33,
      id: "",
      progressive: false,
      progress: 0,
      status: "",
      showProgress: false,
      canGenereate: true,
      timer: null,
      download: false,
      base: "https://audiolemon.herokuapp.com",

    };
  },
  methods: {
    downloadVideo() {
      window.open(this.base + "/video/" + this.id);
      this.download = false;
      this.showProgress = false;
      this.status = "";
      this.canGenereate = true;
    },
    updateProgress(data) {
      this.progress = new Number(data);
      if (this.progress == 100) {
        console.log("stop timer");
        clearInterval(this.timer);
        this.status = "video generated";
        this.download = true;
      }
    },
    success() {
      this.status = "generating vdieo...";
      this.progressive = false;
      this.canGenereate = false;
      this.checkProgress();
    },
    error(e) {
      alert("An error occured");
      console.log(e.responseText);
      this.progressive = false;
      this.showProgress = false;
      this.status = "";
    },
    async click() {
      if (this.canGenereate) {
        this.showProgress = true;
        this.progressive = true;
        this.status = "uploading and initializing...";

        var meta = script.template_01(
          this.header,
          this.subHeader,
          this.description
        );
        var data = new FormData();

        var logo = await this.convertToBlob(require("@/assets/logo.png"))
        var background = await this.convertToBlob(require("@/assets/background.jpg"))
        
         data.append("image_01", this.image, JSON.stringify(meta.foreground));
          data.append("image_02", background, JSON.stringify(meta.background));
         data.append("image_03", logo, JSON.stringify(meta.logo));

          data.append("audio", this.audio);

        data.append("text_01", JSON.stringify(meta.header));
        data.append("text_02", JSON.stringify(meta.subHeader));
        data.append("text_03", JSON.stringify(meta.description));

        data.append("meta", JSON.stringify(meta.meta));
        data.append("id", this.generateID());

        await $.ajax({
          url: this.base + "/generate",
          //url: "http://localhost:8082/generate",
          type: "POST",
          data: data,
          contentType: false,
          processData: false,
          success: this.success,
          error: this.error
        });
      } else {
        alert("A video task is already invoked");
      }
    },
    generateID() {
      let s4 = () => {
        return Math.floor((1 + Math.random()) * 0x10000)
          .toString(16)
          .substring(1);
      };
      var gid = s4() + s4() + "_" + s4();
      this.id = gid;
      return gid;
    },
    slideFoward() {
      if (this.model >= 0 && this.model < 1) {
        this.model++;
      }
    },
    slideBackward() {
      if (this.model >= 0) {
        this.model--;
      }
    },
    checkProgress() {
      this.timer = setInterval(() => {
        $.get(this.base + "/progress/" + this.id).done(this.updateProgress);
      }, 2000);
    },
    async convertToBlob(url) {
      return fetch(url)
          .then(function(response) {
            return response.blob();
          })
          .then(function(blob) {
            return blob
          });
    }
  }
};
</script>

<style>
#app {
  background-color: #333333;
}

.v-application--wrap {
  min-height: 100%;
}
.v-progress-linear {
  margin-top: 50px;
}
.template-wrap .v-responsive__content {
  display: flex;
  justify-content: center;
  background-color: #3f3f3f;
  padding: 50px 0 50px 0;
  border-radius: 20px;
}
.dl-btns {
  display: flex;
  justify-content: space-between;
}
.task-label {
  color: #d1d1d1;
  font-size: 14px;
}
.v-application p {
  margin-bottom: 0px;
}
.template {
  height: 100%;
}
.create-wrapper {
  position: relative;
  padding: 0 60px 0 60px;
  height: 100%;
  width: 1900px;
}
.main-content {
  background-color: #333333;
  display: grid;
  grid-template-columns: 55% 45%;
  grid-auto-rows: auto;
  grid-template-areas: "left right";
  padding: 150px 50px 150px 50px;
}
.carousel {
  grid-area: left;
  padding-right: 150px;
}
.form-section {
  background-color: #3f3f3f;
  grid-area: right;
  padding: 50px 150px 50px 150px;
  border-radius: 20px;
}
.control-btns {
  display: flex;
  justify-content: center;
  padding: 20px;
}
</style>